.product {
  background: #fff;
  text-align: center;
  width: 250px;
  min-width: 250px;
  height: 250px;
  margin: 15px;
  display: inline-block;
}

.cart-box {
  display: block;
  position: relative;
  height: 180px;
  width: 300px;
}

.product > .cart > .left {
  text-align: center;
  width: 40px;
}
.product > .cart > .leftdiscount-tag {
  background: #de2256;
  padding: 1px 5px;
  font-size: 14px;
  color: #fff;
  border-radius: 3px;
  display: inline;
}
.product button {
  background: #de2256;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 5px 15px;
  line-height: 20px;
