}

.product-image {
  position: relative;
  display: block;
  width: 100px;
  height: 100px;
}

.discount-tag {
  background: #de2256;
  padding: 1px 5px;
  font-size: 14px;
  color: #fff;
  border-radius: 3px;
  display: inline;
  position: absolute;
  left: 1px;
  top: 1px;
}

.cart-bottom {
  height: 20%;
  border-top: 1px solid #de2256;
